@import '../../style/colors.css';

.user-details {
	display: flex;
}

.cart {
	position: relative;
}

.user-img {
	height: 2.5rem;
	width: 2.5rem;
	margin-left: 1em;
	border: 2px solid transparent;
	border-radius: 50%;
	cursor: pointer;
	transition: border 0.3s;
}

.user-img:hover {
	border: 2px solid var(--orange);
}

@media (min-width: 992px) {
	.user-img {
		height: 4rem;
		width: 4rem;
		margin-left: 2.5em;
	}
}
