@import '../../style/colors.css';

.button {
	width: 100%;
	padding: 1.2em;
	background-color: var(--orange);
	border: none;
	border-radius: 1rem;
	outline: none;
	color: var(--white);
	font-weight: bold;
	cursor: pointer;
	transition: background-color 0.3s;
}

.button:hover {
    background-color: var(--pale-orange);
}
