@import '../../style/colors.css';

.box {
	position: absolute;
	top: -30%;
	right: -30%;
	width: fit-content;
	display: none !important;
	padding: 0.2em 0.8em;
	background-color: var(--orange);
	color: var(--white);
	border-radius: 1rem;
	font-size: 0.8rem;
}

.visible {
	display: block !important;
}
