@import '../../style/colors.css';

.container {
	display: flex;
	justify-content: space-between;
	margin-top: 2em;
	width: 45rem;
}

.thumbnail {
	display: block;
	height: 10rem;
	border: 2px solid transparent;
	border-radius: 1rem;
	cursor: pointer;
}

.thumbnail:hover {
	border: 2px solid var(--orange);
	opacity: 0.8;
}

.active {
	border: 2px solid var(--orange);
	opacity: 0.8;
}
