@import '../../style/colors.css';

.button {
	position: absolute;
	top: 25%;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 4rem;
	width: 4rem;
	background-color: var(--light-grayish-blue);
	border-radius: 50%;
	border: none;
	outline: none;
	cursor: pointer;
	transition: background-color 0.3s;
}

.button:hover {
	background-color: var(--white);
}
