.carousel {
	display: flex;
	height: 25rem;
	width: 100%;
	transition: transform 0.3s;
}

.carousel img {
	width: 100%;
	object-fit: cover;
}

.previous {
	left: 0.5rem;
}

.next {
	right: 0.5rem;
}

@media (min-width: 992px) {
	.carousel {
		display: none;
	}

	.previous,
	.next {
		display: none;
	}
}
