@import '../../style/colors.css';

.cart {
	position: absolute;
	top: 10%;
	width: 96%;
	right: 50%;
	transform: translateX(50%);
	padding: 0;
	background-color: var(--white);
	border-radius: 1rem;
	box-shadow: 3px 8px 22px -15px var(--very-dark-blue);
	z-index: 5;
}

.cart h2 {
	padding: 1.5em;
	font-size: 1.6rem;
	color: var(--very-dark-blue);
}

.cart hr {
	height: 1px;
	background-color: var(--grayish-blue);
	border: 0;
}

.cart-items {
	padding: 1.5em;
	text-align: center;
}

.warning {
	padding: 5em 0;
	font-weight: bold;
}

@media (min-width: 992px) {
	.cart {
		width: 30%;
		right: 10%;
		transform: translateX(0);
	}
}

@media (min-width: 1600px) {
	.cart {
		right: 25%;
		max-width: 400px;
	}
}

@media (min-width: 3000px) {
	.cart {
		position: fixed;
		top: 4%;
		left: 55%;
		max-width: 400px;
	}
}
