@import '../../style/colors.css';

.footer {
    padding: 2em 1em;
	font-size: 1.2rem;
	text-align: center;
}

.footer a {
	text-decoration: none;
	color: var(--orange);
	transition: color 0.3s;
}

.footer a:hover {
	color: var(--orange-hover);
}

@media (min-width: 992px) {
    .footer {
        padding: 6em 1em 2em;
        font-size: 1.4rem;
    }
}