@import '../../style/colors.css';

.nav-desktop {
	display: none;
	width: 80%;
	max-width: 110rem;
	justify-content: space-between;
	align-items: center;
	padding: 1.5em 0 3em;
	border-bottom: 1px solid var(--grayish-blue);
}

.nav-desktop div {
	display: flex;
	align-items: center;
}

.nav-links {
	margin-left: 3em;
	cursor: pointer;
}

.nav-link {
	position: relative;
	margin: 0 1em;
	transition: color 0.3s;
}

.nav-link::before {
	position: absolute;
	top: 4.7em;
	left: 0;
	right: 0;
	transform: scaleX(0);
	transform-origin: left;
	height: 4px;
	background-color: var(--orange);
	content: '';
	transition: transform 0.3s;
}

.nav-link:hover,
.nav-link:active {
	color: var(--very-dark-blue);
}

.nav-link:hover::before {
	transform: scaleX(1);
}

.nav-link:active::before {
	transform: scaleX(1);
}

@media (min-width: 992px) {
	.nav-desktop {
		display: flex;
	}
}
