@import '../../style/colors.css';

.container {
	display: flex;
	margin-bottom: 1.5em;
}

.thumbnail-img {
	height: 5rem;
	width: 5rem;
	margin-right: 1em;
	border-radius: 0.5rem;
}

.description {
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	line-height: 1.5;
}

.description h3 {
	font-size: 1.2rem;
	font-weight: 400;
}

.prices {
	display: flex;
	font-size: 1.4rem;
}

.total {
	margin-left: 0.5em;
	color: var(--very-dark-blue);
	font-weight: bold;
}

@media (min-width: 1200px) {
	.description h3 {
		font-size: 1.6rem;
	}

	.prices {
		font-size: 1.6rem;
	}
}
