@import '../../style/colors';

.container {
	z-index: 10;
}

.carousel {
	position: relative;
	display: flex;
	height: 45rem;
	width: 45rem;
}

.carousel img {
	display: none;
	border-radius: 1rem;
}

.carousel .visible {
	display: block;
}

.close {
	position: absolute;
	right: 0;
	top: -3rem;
}

.previous,
.next {
	top: 50%;
	transform: translateY(-50%);
}

.previous {
	left: -1.8rem;
}

.next {
	right: -1.8rem;
}
