@import '../../style/colors.css';

.container {
	display: flex;
	justify-content: space-between;
}

.visible {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	transform: translateX(0);
	background-color: var(--shadow-black);
	transition: transform 0.8s;
	z-index: 10;
}

.hidden {
	transition: transform 0.8s;
	transform: translateX(-150%);
}

.nav-background {
	height: 100%;
	width: 60%;
	padding: 1.5em;
	background-color: var(--white);
	z-index: 5;
}

.nav-links {
	display: flex;
	flex-direction: column;
}

.nav-links a {
	padding: 0.8em 0;
	color: var(--very-dark-blue);
	font-weight: bold;
}

.open-btn {
	margin-right: 1em;
}

.close-btn {
	margin-bottom: 2em;
}

@media (min-width: 992px) {
	.nav-mobile {
		display: none;
	}
}
