@import '../../style/colors.css';

form {
	margin-top: 1em;
}

.input-control {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	margin-bottom: 1em;
	background-color: var(--light-grayish-blue);
	border-radius: 1rem;
}

.input-control input {
	background-color: transparent;
	border: none;
	outline: none;
	color: var(--very-dark-blue);
	font-weight: bold;
	width: 5rem;
	text-align: center;
}

.change-quantity {
	padding: 1em;
	background-color: transparent;
	border: none;
	outline: none;
	cursor: pointer;
}

.button {
	display: flex;
	align-items: center;
	justify-content: center;
}

.button p {
	margin-left: 0.5em;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.error {
	margin-top: 1em;
	color: var(--error);
	text-align: center;
	font-weight: bold;
}


@media (min-width: 992px) {
	.form-controls {
		display: flex;
		justify-content: space-between;
		margin-top: 2em;
	}

	.input-control {
		justify-content: space-evenly;
		width: 25rem;
		margin: 0 1em 0em 0;
	}
}
