@import '../../style/colors.css';

.container {
	width: 100%;
}

.brand {
	padding-bottom: 1em;
	font-size: 1.1rem;
	color: var(--orange);
	text-transform: uppercase;
	font-weight: bold;
	letter-spacing: 2px;
}

.model {
	padding-bottom: 0.5em;
	font-size: 2.4rem;
	color: var(--very-dark-blue);
}

.description {
	font-size: 1.2rem;
	line-height: 1.8;
	padding-bottom: 1.5em;
}

.pricing {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.discounted-pricing {
	display: flex;
	align-items: center;
	margin-bottom: 0.5em;
}

.discounted-price {
	color: var(--very-dark-blue);
}

.discount {
	display: inline;
	padding: 0.2em;
	margin-left: 1em;
	border-radius: 1rem;
	color: var(--orange);
	font-weight: bold;
	background-color: var(--pale-orange);
}

.original-price {
	color: var(--grayish-blue);
	text-decoration: line-through;
}

@media (min-width: 992px) {
	.container {
		max-width: 45rem;
		margin-left: 1em;
	}

	.brand {
		font-size: 1.6rem;
	}

	.model {
		padding-bottom: 0.8em;
		font-size: 3.5rem;
	}

	.description {
		font-size: 1.6rem;
		line-height: 1.5;
	}

	.pricing {
		display: block;
	}
}
